<template>
  <div class="login">
    <el-card>
      <h2>NETFLIX CODE</h2>
      <el-form
          class="login-form"
          :model="model"
          :rules="rules"
          ref="form"
          @submit.native.prevent="login"
      >
        <el-form-item prop="email">
          <el-input v-model="model.email" placeholder="Email" prefix-icon="fas fa-user"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
              :loading="loading"
              class="login-button"
              type="primary"
              native-type="submit"
              block
          >Query
          </el-button>
        </el-form-item>
        <a class="forgot-password" @click="open2">No code ?</a>
      </el-form>
    </el-card>
  </div>
</template>

<script>

export default {
  name: "login",
  data() {
    var checkEmail = (rule, value, callback) => {
          const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
          if (!value) {
            return callback(new Error('Email is required'))
          }
          setTimeout(() => {
            if (mailReg.test(value)) {
              callback()
            } else {
              callback(new Error('Email address is not valid'))
            }
          }, 100)
        }
    return {
      model: {
        email: ""
      },
      loading: false,
      rules: {
        email: [
          {
            validator: checkEmail,
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    simulateLogin() {
      return this.$http.get('https://apicode.4knfnf.com/query',{params:{email:this.model.email}}).then((response) => {
        return response.data.code;
      }).catch(() => {
        return "ERROR";
      })

    },
    async login() {
      let valid = await this.$refs.form.validate();
      if (valid) {
        this.loading = true;
        let code = await this.simulateLogin();
        if (code == "0") {
          let messbox = '<p>Click "Verify by email" or try again later</p>';
          this.loading = false;
          this.$alert(messbox, 'YOUR CODE', {
            confirmButtonText: 'Got it!',
            dangerouslyUseHTMLString: true,
            center: true
          });
        } else {
          let messbox = '<p class="p-code">' + code + "</p>";
          this.loading = false;
          this.$alert(messbox, 'YOUR CODE', {
            confirmButtonText: 'Got it!',
            dangerouslyUseHTMLString: true,
            center: true
          });
        }
      }
    },
    open2() {
      this.$alert('Try again later ~', 'NO CODE?', {
        confirmButtonText: 'Got it!',
        center: true
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-button {
  width: 100%;
  margin-top: 40px;
}

.login-form {
  width: 290px;
}

.forgot-password {
  margin-top: 10px;
}
</style>
<style lang="scss">
$teal: rgb(222, 9, 20);
.el-button--primary {
  background: $teal;
  border-color: $teal;

  &:hover,
  &.active,
  &:focus {
    background: lighten($teal, 7);
    border-color: lighten($teal, 7);
  }
}

.p-code {
  letter-spacing: 10px;
  font-size: 50px;
}

.login .el-input__inner:hover {
  border-color: $teal;
}

.login .el-input__prefix {
  background: rgb(238, 237, 234);
  left: 0;
  height: calc(100% - 2px);
  left: 1px;
  top: 1px;
  border-radius: 3px;

  .el-input__icon {
    width: 30px;
  }
}

.login .el-input input {
  padding-left: 35px;
}

.login .el-card {
  padding-top: 0;
  padding-bottom: 30px;
}

h2 {
  font-family: "Open Sans";
  letter-spacing: 1px;
  font-family: Roboto, sans-serif;
  padding-bottom: 20px;
}

a {
  color: $teal;
  text-decoration: none;

  &:hover,
  &:active,
  &:focus {
    color: lighten($teal, 7);
  }
}

.login .el-card {
  width: 340px;
  display: flex;
  justify-content: center;
}
</style>
